import * as React from "react";
import styled from "styled-components";
import { mdCss } from "@layout/mdCss";
import { colors, device } from "@layout/styleConfig";

const Layout = styled.div.withConfig({ componentId: "BlogPost_Layout" })`
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  padding: 32px 0;
  color: ${colors.white};

  .post-title {
    font-size: 32px;
    font-weight: 800;
    grid-column-start: 3;
    grid-column: 1/-1;
  }
  .post-date {
    grid-column: 1/-1;
    color: ${colors.darkWhite};
    margin-top: 8px;
  }

  img {
    grid-column: 1/-1;
    width: 100%;
    height: 200px;
    object-fit: cover;
    margin-top: 24px;
    border-radius: 16px;
  }

  .post-content {
    grid-column: 1/-1;
    color: ${colors.lightWhite};

    ${mdCss};
  }

  @media ${device.tablet} {
    .post-title {
      grid-column: 2/-2;
      font-size: 36px;
    }
    .post-date {
      grid-column: 2/-2;
    }

    img {
      grid-column: 2/-2;
      height: 300px;
    }

    .post-content {
      grid-column: 2/-2;
    }
  }

  @media ${device.laptop} {
    .post-title {
      grid-column: 3/-3;
      font-size: 40px;
    }
    .post-date {
      grid-column: 3/-3;
    }

    img {
      grid-column: 3/-3;
      height: 400px;
    }

    .post-content {
      grid-column: 3/-3;
    }
  }
`;

export default function BlogPost({ data }) {
  const {
    title,
    date,
    image_cover,
    // tags,
    // type,
    html,
  } = data;
  return (
    <Layout>
      <div className="post-title">{title}</div>
      <div className="post-date">{date}</div>
      <img src={image_cover} alt={title} />
      <div
        className="post-content"
        dangerouslySetInnerHTML={{ __html: html }}
      />
    </Layout>
  );
}
